.outer {
  width: 90%;
  height: 5%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-top: 5px;
}
.outer div {
  border: 2px solid #ccc;
  border-radius: 5px;
  padding: 5px;
  box-shadow: 1px 2px 3px #ccc;
  font-family: sans-serif;
}
div select {
  margin: 5px;
  border-radius: 3px;
  width: auto;
  height: 30px;
}
