/* first div tag */
.cartitems {
  margin: 10px;
}

/* second div tag */

.cart-ul {
  list-style-type: none;
  padding: 0px;
}
.cart-outer-div {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  align-items: center;
  border: 2px solid #eee;
  border-radius: 5px;
  box-shadow: 1px 2px 3px #ccc;
  margin: 5px;
  padding: 5px;
}
.card-inner-div h6 {
  margin-top: 5px;
}
.card-inner-div div button {
  margin-left: 100px;
}
.cart-img {
  width: 75px;
  height: 75px;
}
.remove-btn {
  color: rgb(102, 91, 91);
  font-weight: bolder;
}

/* third div tag */

.Proceed {
  border: 2px solid #eee;
  border-radius: 5px;
  box-shadow: 1px 2px 3px #ccc;
  padding: 10px;
}
.Proceed-btn {
  margin-left: 80px;
  font-weight: bolder;
  color: white;
}

/* fourth div tag */

.checkout {
  border: 2px solid #eee;
  border-radius: 5px;
  box-shadow: 1px 2px 3px #ccc;
  margin-top: 10px;
}

.checkout div {
  padding: 10px;
}

.checkout div label {
  width: 100%;
  font-weight: bolder;
}

.checkout div input {
  width: 100%;
  height: 40px;
  border-radius: 5px;
  border: 2px solid #ccc;
  color: black;
}

.checkout div button {
  width: 100%;
  font-weight: bolder;
}
