.tshirt-ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.tshirt-li {
  flex: 20rem;
  height: 27rem;
  padding: 1rem;
}
.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  max-width: 305px;
}
.card img {
  height: 16rem;
  max-width: 37rem;
  max-height: 37rem;
}
.both-modal {
  display: flex;
  flex-wrap: wrap;
}
.first-modal {
  width: 50%;
}
.first-modal img {
  margin: 5%;
  width: 70%;
  height: 100%;
  box-shadow: 1px 2px 3px #eee;
  border-radius: 5px;
  border: 2px solid #ccc;
}
.second-modal {
  width: 50%;
}
.close-btn {
  border-radius: 5px;
  font-weight: bolder;
  background-color: white;
  box-shadow: 1px 2px 3px #eee;
  float: right;
  cursor: pointer;
}

.size-btn {
  margin: 5px;
  font-weight: bolder;
}

.checkout-btn {
  width: 100%;
  padding: 1%;
  margin: 1%;
  font-size: 18px;
}
